'use strict';

/**
* Convert a scalar value to a zero-dimensional ndarray.
*
* @module @stdlib/ndarray-base-from-scalar
*
* @example
* var scalar2ndarray = require( '@stdlib/ndarray-base-from-scalar' );
*
* var x = scalar2ndarray( 1.0, 'float64' );
* // returns <ndarray>
*
* var sh = x.shape;
* // returns []
*
* var dt = x.dtype;
* // returns 'float64'
*
* var v = x.get();
* // returns 1.0
*/

// MODULES //

var main = require( './main.js' );


// EXPORTS //

module.exports = main;
